import { Disposable } from "@hediet/std/disposable";
import { observable, runInAction } from "mobx";
import { IReference } from "./ref";

export class UrlHashStore
	implements IReference<string | undefined>, Disposable
{
	@observable private value: string | undefined;
	public readonly dispose = Disposable.fn();
	constructor() {
		const updateValue = () => {
			const newValue = window.location.hash.substr(1);
			if (this.value !== newValue) {
				runInAction("hash changed, update value", () => {
					this.value = newValue;
				});
			}
		};

		updateValue();

		window.addEventListener("hashchange", updateValue);
		this.dispose.track({
			dispose: () => {
				window.removeEventListener("hashchange", updateValue);
			},
		});
	}

	get(): string | undefined {
		return this.value;
	}

	set(value: string): void {
		this.value = value;
		history.replaceState("", "", "#" + value);
	}
}
