import { Disposable } from "@hediet/std/disposable";
import {
	action,
	autorun,
	computed,
	observable,
	reaction,
	runInAction,
} from "mobx";
import {
	getLoadedMonaco,
	IMonacoSetup,
	loadMonaco,
} from "../../../monaco-loader";
import { IPlaygroundProject, IPreviewState } from "../../../shared";
import { getPlaygroundExamples, PlaygroundExample } from "./playgroundExamples";
import { UrlHashStore } from "../../utils/UrlHashStore";
import {
	JsonString,
	Settings,
	SettingsModel,
	toLoaderConfig,
} from "./SettingsModel";
import { Debouncer } from "../../utils/Debouncer";
import { LzmaCompressor } from "../../utils/lzmaCompressor";
import { ObservablePromise } from "../../utils/ObservablePromise";

export class PlaygroundModel {
	@observable
	public html = "";

	@observable
	public js = "";

	@observable
	public css = "";

	@observable
	public reloadKey = 0;

	reload(): void {
		this.reloadKey++;
	}

	private readonly _previewHandlers = new Set<IPreviewHandler>();

	public readonly settings = new SettingsModel();

	@computed
	public get state(): IPreviewState {
		const state: IPreviewState = {
			html: this.html,
			js: this.js,
			css: this.css,
			monacoSetup: this.settings.monacoSetup,
		};

		return state;
	}

	private readonly urlHashStore = new UrlHashStore();

	@observable
	public settingsDialogModel: SettingsDialogModel | undefined = undefined;

	@observable.ref
	private _selectedExample: PlaygroundExample | undefined;

	@observable.ref
	public selectedExampleProject: IPlaygroundProject | undefined;

	public get selectedExample(): PlaygroundExample | undefined {
		return this._selectedExample;
	}

	public set selectedExample(value: PlaygroundExample | undefined) {
		this._selectedExample = value;
		this.selectedExampleProject = undefined;
		if (value) {
			value.load().then((p) => {
				runInAction("update example", () => {
					this.selectedExampleProject = p;
					this.setState(p);
				});
			});
		}
	}

	public readonly dispose = Disposable.fn();
	private readonly serializer = new StateUrlSerializer(
		this,
		this.urlHashStore
	);

	private readonly debouncer = new Debouncer(250);

	constructor() {
		this.dispose.track({
			dispose: reaction(
				() => ({ state: this.state, key: this.reloadKey }),
				({ state }) => {
					this.debouncer.run(() => {
						for (const handler of this._previewHandlers) {
							handler.handlePreview(state);
						}
					});
				},
				{ name: "update preview" }
			),
		});

		const observablePromise = new ObservablePromise(loadMonaco());
		let disposable: Disposable | undefined = undefined;

		this.dispose.track({
			dispose: autorun(async () => {
				const monaco = observablePromise.value;
				if (!monaco) {
					return;
				}
				const monacoTypesUrl = this.settings.monacoSetup.monacoTypesUrl;
				let content = "";
				if (monacoTypesUrl) {
					content = await (await fetch(monacoTypesUrl)).text();
				}
				if (disposable) {
					disposable.dispose();
					disposable = undefined;
				}

				if (content) {
					disposable =
						monaco.languages.typescript.javascriptDefaults.addExtraLib(
							content,
							"ts:monaco.d.ts"
						);
				}
			}),
		});
	}

	public showSettingsDialog(): void {
		this.settingsDialogModel = new SettingsDialogModel(
			this.settings.settings
		);
	}

	public closeSettingsDialog(acceptChanges: boolean): void {
		if (!this.settingsDialogModel) {
			return;
		}
		if (acceptChanges) {
			this.settings.setSettings(this.settingsDialogModel.settings);
		}
		this.settingsDialogModel = undefined;
	}

	@action
	public setState(state: IPlaygroundProject) {
		this.html = state.html;
		this.js = state.js;
		this.css = state.css;
	}

	public setPreviewHandler(handler: IPreviewHandler): monaco.IDisposable {
		this._previewHandlers.add(handler);
		handler.handlePreview(this.state);
		return {
			dispose: () => {
				this._previewHandlers.delete(handler);
			},
		};
	}
}

export interface IPreviewHandler {
	handlePreview(state: IPreviewState): void;
}

export class SettingsDialogModel {
	@observable settings: Settings;

	@computed get monacoSetupJsonString(): JsonString<IMonacoSetup> {
		if (this.settings.monacoSource === "custom") {
			return this.settings.customConfig;
		}

		return JSON.stringify(toLoaderConfig(this.settings), undefined, 4);
	}

	constructor(settings: Settings) {
		this.settings = Object.assign({}, settings);
	}
}

class StateUrlSerializer {
	public readonly dispose = Disposable.fn();
	private readonly debouncer = new Debouncer(500);

	private readonly compressor = new LzmaCompressor<IPlaygroundProject>();

	constructor(
		model: PlaygroundModel,
		private readonly urlHashStore: UrlHashStore
	) {
		let isHashUpdating = false;

		this.dispose.track({
			dispose: autorun(
				() => {
					const hashValue = this.urlHashStore.get();
					isHashUpdating = true;
					try {
						if (!hashValue) {
							model.selectedExample =
								getPlaygroundExamples()[0].examples[0];
						} else if (hashValue.startsWith("example-")) {
							const exampleName = hashValue.substring(
								"example-".length
							);
							const example = getPlaygroundExamples()
								.flatMap((e) => e.examples)
								.find((e) => e.id === exampleName);
							if (example) {
								model.selectedExample = example;
							}
						} else {
							try {
								model.setState(
									this.compressor.decodeData(hashValue)
								);
							} catch (e) {
								console.log(
									"Could not deserialize from hash value",
									e
								);
							}
						}
					} finally {
						isHashUpdating = false;
					}
				},
				{ name: "update state from hash" }
			),
		});

		this.dispose.track({
			dispose: reaction(
				() => ({
					state: model.state,
					_selectedExampleProject: model.selectedExampleProject,
				}),
				({ state, _selectedExampleProject }) => {
					if (isHashUpdating) {
						return;
					}
					this.debouncer.run(() => {
						let hash = "";
						if (
							_selectedExampleProject &&
							state.css === _selectedExampleProject.css &&
							state.js === _selectedExampleProject.js &&
							state.html === _selectedExampleProject.html
						) {
							hash = "example-" + model.selectedExample!.id;
						} else {
							hash = this.compressor.encodeData(state);
						}

						this.urlHashStore.set(hash);
					});
				},
				{ name: "update hash" }
			),
		});
	}
}
